h1{
  color: #fff;
  letter-spacing: 10px;
}

header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 20px 0;
}

header img:hover{
  cursor: pointer;
}

.appWrapperlight{
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  width: 100%;
  min-height: 100vh;
  background-image: url('bg-desktop-light.jpg');
  background-repeat: no-repeat;
  background-color: hsl(236, 33%, 92%);
}

.container{
  width: 50vw;
}

input{
  width: 80%;
  margin: 0;
  height: 15px;
  padding: 15px;
  border: 0;
  border-radius: 5px;
}

input:focus{
  outline-width: 0;
}

.newItem{
  display: flex;
  align-items: center;
  background-color: #fff;
  border-radius: 5px;
  margin: 25px 0;
}

footer{
  display: flex;
  justify-content: space-between;
  margin: 5px 0;
  padding: 8.5px;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0 0 5px rgb(180, 180, 180);
  color: hsl(236, 9%, 61%);
}
footer p{
  margin: 5px;
}

footer p:hover{
  cursor: pointer;
  color: hsl(235, 19%, 35%);
}

.showed{
  display: flex;
  justify-content: space-between;
}

.newElement{
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #fff;
  border-radius: 5px;
  margin: 5px 0;
  box-shadow: 0 0 5px rgba(119, 119, 119, 0.548);
}

.mostra{
  display: none;
}

.checking{
  display: flex;
  align-items: center;
}

.checking:hover{
  cursor: pointer;
}

.delete{
  margin: 10px;
}

.delete:hover{
  cursor: pointer;
}

.setcompleted{
  border-radius: 50%;
}

.selected{
  color: hsl(280, 46%, 51%);
}
.selected p:hover{
  color: hsl(280, 46%, 51%);
}

.newElement.all.mostra{
  display: none;
}

/* darkmode */

.appWrapperdark{
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  width: 100%;
  min-height: 100vh;
  background-image: url('bg-desktop-dark.jpg');
  background-repeat: no-repeat;
  background-color: hsl(237, 14%, 26%);
}

.appWrapperdark .newItem{
  background-color: hsl(233, 14%, 35%);
}

.appWrapperdark input{
  background-color: hsl(233, 14%, 35%);
  color: #fff;
}

.appWrapperdark input::placeholder{
  color: #fff;
}

.appWrapperdark footer{
  background-color: hsl(233, 14%, 35%);
  box-shadow: 0 0 5px rgba(114, 113, 113, 0.342)
}

.appWrapperdark .newElement{
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: hsl(233, 14%, 35%);
  color: #fff;
  border-radius: 5px;
  margin: 5px 0;
  box-shadow: 0 0 5px rgba(114, 113, 113, 0.342);
}

.appWrapperdark footer p:hover{
  cursor: pointer;
  color: #fff;
}

.appWrapperdark .selected p:hover{
  color: hsl(280, 46%, 51%);
}

.appWrapperdark.mostra{
  display: none;
}

.reorder{
  text-align: center;
  color: hsl(236, 9%, 61%);
  margin: 50px 0;
}

@media screen and (min-width: 701px) {
  .mobile{
    display: none;
  }
}

@media screen and (max-width: 700px) {
  .container{
    width: 85%;
  }

  .desktop{
    display: none;
  }

  footer{
    background-color: #ffffff00;
    box-shadow: 0 0 0 #ffffff00;
    padding: 0;
  }

  .appWrapperdark footer{
    background-color: #ffffff00;
    box-shadow: 0 0 0 #ffffff00;
  }

  .mobile{
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .mobile .showed{
    justify-content: center;
    background-color: #fff;
    margin: 10px 0;
    height: 25px;
    border-radius: 5px;
    padding: 10px;
  }

  .firstRow{
    display: flex;
    justify-content: space-between;
    background-color: #fff;
    height: 25px;
    border-radius: 5px;
    padding: 10px;
  }

  .appWrapperlight{
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    min-height: 100vh;
    background-image: url('bg-mobile-light.jpg');
    background-repeat: no-repeat;
    background-size: contain;
    background-color: hsl(236, 33%, 92%);
  }

  .appWrapperdark{
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    min-height: 100vh;
    background-image: url('bg-mobile-dark.jpg');
    background-repeat: no-repeat;
    background-size: contain;

    background-color: hsl(237, 14%, 26%);
  }

  .appWrapperdark footer p:hover{
    cursor: pointer;
    color: hsl(236, 9%, 61%);
  }

  .appWrapperlight footer p:hover{
    cursor: pointer;
    color: hsl(236, 9%, 61%);
  }
}