.check{
    display: flex;
    border: 1px solid hsl(280, 94%, 87%);
    border-radius: 50%;
    width: 20px;
    height: 20px;
    margin: 10px;
  }

  .check:hover{
    cursor: pointer;
    border: 1px solid rgb(161, 161, 161);
  }
  
  .checked{
    display: flex;
    margin: 10px;
    border: 1px solid hsl(280, 94%, 87%);
    border-radius: 50%;
    width: 20px;
    height: 20px;
    background-image: url('icon-check.svg'), linear-gradient(hsl(192, 100%, 67%), hsl(280, 87%, 65%));
    background-repeat: no-repeat;
    background-position: center;
  }

  .checked:hover{
    cursor: pointer;
    border: 1px solid rgb(161, 161, 161);
  }